import type { Metadata } from "next";
import { notFound } from "next/navigation";
import Link from "next/link";
import { CITY_PROFILES, getCityProfile } from "@/data/cities";
import { getStateByAbbr } from "@/data/states";
import { RoundSkyWidget } from "@/components/round-sky-widget";
import { TcpaConsent } from "@/components/compliance/tcpa-consent";
import { FaqList } from "@/components/faq/faq-list";

export const dynamicParams = false;

export function generateStaticParams() {
  return CITY_PROFILES.map((city) => ({
    state: city.stateAbbr.toLowerCase(),
    city: city.slug,
  }));
}

export async function generateMetadata({ params }: { params: Promise<{ state: string; city: string }> }): Promise<Metadata> {
  const { state: stateParam, city } = await params;
  const cityProfile = getCityProfile(stateParam, city);
  const state = getStateByAbbr(stateParam);
  if (!cityProfile || !state) return { title: "Ciudad no encontrada" };

  return {
    title: `Préstamos en ${cityProfile.city}, ${state.abbr}`,
    description: `Información sobre préstamos y regulaciones en ${cityProfile.city}, ${state.name}.`,
    alternates: {
      canonical: `https://www.loanaiportal.com/es/cities/${state.abbr.toLowerCase()}/${cityProfile.slug}`,
    },
  };
}

export default async function CityEsPage({ params }: { params: Promise<{ state: string; city: string }> }) {
  const { state: stateParam, city } = await params;
  const cityProfile = getCityProfile(stateParam, city);
  const state = getStateByAbbr(stateParam);
  if (!cityProfile || !state) return notFound();

  return (
    <div className="mx-auto max-w-5xl space-y-12 px-4 py-16 lg:px-8">
      <nav className="text-sm text-slate-500" aria-label="Breadcrumb">
        <ol className="flex items-center gap-2">
          <li>
            <Link href="/es" className="hover:text-slate-700">
              Inicio
            </Link>
          </li>
          <li aria-hidden>/</li>
          <li>
            <Link href="/es/cities" className="hover:text-slate-700">
              Ciudades
            </Link>
          </li>
          <li aria-hidden>/</li>
          <li className="text-slate-900 font-semibold">
            {cityProfile.city}, {state.abbr}
          </li>
        </ol>
      </nav>

      <header className="space-y-4">
        <h1 className="text-4xl font-semibold text-slate-900">
          Préstamos en {cityProfile.city}, {state.abbr}
        </h1>
        <p className="text-base text-slate-600">
          Contenido en español para comparar préstamos a plazos, personales y alternativas de payday en {cityProfile.city}.
        </p>
      </header>

      <section className="rounded-3xl border border-slate-200 bg-white/80 p-6 shadow-sm">
        <h2 className="text-2xl feont-semibold text-slate-900">Calculadora</h2>
        <div className="mt-4 rounded-3xl border border-white/70 bg-white/95 p-4 shadow-inner">
          <RoundSkyWidget
            intent={`city-es-${cityProfile.slug}`}
            pageId={`city-es-${state.abbr.toLowerCase()}-${cityProfile.slug}`}
          />
        </div>
  <TcpaConsent locale="es" privacyHref="/es/privacy" termsHref="/es/terms" />
      </section>

      <section className="grid gap-6 md:grid-cols-2">
        <article className="rounded-3xl border border-slate-200 bg-white/80 p-6 shadow-sm">
          <h2 className="text-xl font-semibold text-slate-900">Datos clave</h2>
          <p className="text-sm text-slate-600">
            Ingreso promedio: ${cityProfile.avgHouseholdIncome.toLocaleString()} · Desempleo: {cityProfile.unemploymentRate}%.
          </p>
        </article>
        <article className="rounded-3xl border border-slate-200 bg-white/80 p-6 shadow-sm">
          <h2 className="text-xl font-semibold text-slate-900">Siguiente paso</h2>
          <p className="text-sm text-slate-600">
            Visita el hub de {state.name} para ver límites y reglas completas.
          </p>
          <Link
            href={`/es/states/${state.abbr.toLowerCase()}`}
            className="mt-3 inline-flex text-sm font-semibold text-blue-600"
          >
            Abrir hub →
          </Link>
        </article>
      </section>

      <FaqList locale="es" />
    </div>
  );
}
